import React from 'react';
import { useNavigate } from 'react-router-dom';

const BeautyProducts = [
  {
    name: 'Concealer',
    price: '₹999',
    image: 'data:image/jpeg;base64,',
  },
  {
    name: 'Foundation',
    price: '₹599',
    image: 'data:image/jpeg;base64,',
  },
  {
    name: 'Smooth Powder',
    price: '₹999',
    image: 'data:image/jpeg;base64,',
  },
  {
    name: 'Bruches',
    price: '₹599',
    image: 'data:image/jpeg;base64,',
  },
  {
    name: 'Beauty Blush',
    price: '₹13,999',
    image: 'data:image/jpeg;base64,',
  },
  {
    name: 'Lipsticks',
    price: '₹259',
    image: 'data:image/jpeg;base64,',
  },
];

const Beauty = () => {
  const navigate = useNavigate();

  const handleProductClick = (product) => {
    // Example: Navigate to product details or virtual try-on
    console.log('Clicked:', product.name);
    navigate('/product-details', { state: { product } }); // optional route for expansion
  };

  return (
    <div style={styles.container}>
      <h2 style={styles.heading}>Beauty</h2>
      <div style={styles.grid}>
        {BeautyProducts.map((product, index) => (
          <div
            key={index}
            style={styles.card}
            onClick={() => handleProductClick(product)}
          >
            <img
              src={product.image}
              alt={product.name}
              style={styles.image}
            />
            <div style={styles.name}>{product.name}</div>
            <div style={styles.price}>{product.price}</div>
          </div>
        ))}
      </div>
    </div>
  );
};

const styles = {
  container: {
    padding: '20px',
    backgroundColor: '#f4f6f8',
    minHeight: '100vh',
  },
  heading: {
    textAlign: 'center',
    marginBottom: '20px',
    fontSize: '28px',
    color: '#333',
  },
  grid: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'center',
    gap: '20px',
  },
  card: {
    backgroundColor: '#fff',
    width: '200px',
    padding: '15px',
    borderRadius: '12px',
    boxShadow: '0 4px 8px rgba(0,0,0,0.1)',
    textAlign: 'center',
    cursor: 'pointer',
    transition: 'transform 0.2s ease',
  },
  image: {
    width: '100%',
    height: '180px',
    objectFit: 'contain',
    marginBottom: '10px',
  },
  name: {
    fontSize: '16px',
    fontWeight: '600',
    color: '#333',
  },
  price: {
    fontSize: '15px',
    fontWeight: 'bold',
    color: '#2e7d32',
    marginTop: '5px',
  },
};

export default Beauty;