import React from 'react';
import { useNavigate } from 'react-router-dom';

const MobileProducts = [
  {
    name: 'iPhone 14 Pro',
    price: '₹1,29,999',
    image: 'data:image/jpeg;base64,',
  },
  {
    name: 'Samsung Galaxy S23 Ultra',
    price: '₹1,24,999',
    image: 'data:image/jpeg;base64,',
  },
  {
    name: 'OnePlus 12',
    price: '₹69,999',
    image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSZ5SO_N2kc5R885W9bayZkeUPEf8XzcxJBkQ&s',
  },
  {
    name: 'Google Pixel 8 Pro',
    price: '₹94,999',
    image: 'data:image/jpeg;base64,',
  },
  {
    name: 'Realme Narzo 60X',
    price: '₹13,999',
    image: 'data:image/jpeg;base64,',
    },
  {
    name: 'Redmi Note 13 Pro+',
    price: '₹25,999',
    image: 'data:image/jpeg;base64,',
  },
];

const Mobile = () => {
  const navigate = useNavigate();

  const handleProductClick = (product) => {
    // Example: Navigate to product details or virtual try-on
    console.log('Clicked:', product.name);
    navigate('/product-details', { state: { product } }); // optional route for expansion
  };

  return (
    <div style={styles.container}>
      <h2 style={styles.heading}>Mobile</h2>
      <div style={styles.grid}>
        {MobileProducts.map((product, index) => (
          <div
            key={index}
            style={styles.card}
            onClick={() => handleProductClick(product)}
          >
            <img
              src={product.image}
              alt={product.name}
              style={styles.image}
            />
            <div style={styles.name}>{product.name}</div>
            <div style={styles.price}>{product.price}</div>
          </div>
        ))}
      </div>
    </div>
  );
};

const styles = {
  container: {
    padding: '20px',
    backgroundColor: '#f4f6f8',
    minHeight: '100vh',
  },
  heading: {
    textAlign: 'center',
    marginBottom: '20px',
    fontSize: '28px',
    color: '#333',
  },
  grid: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'center',
    gap: '20px',
  },
  card: {
    backgroundColor: '#fff',
    width: '200px',
    padding: '15px',
    borderRadius: '12px',
    boxShadow: '0 4px 8px rgba(0,0,0,0.1)',
    textAlign: 'center',
    cursor: 'pointer',
    transition: 'transform 0.2s ease',
  },
  image: {
    width: '100%',
    height: '180px',
    objectFit: 'contain',
    marginBottom: '10px',
  },
  name: {
    fontSize: '16px',
    fontWeight: '600',
    color: '#333',
  },
  price: {
    fontSize: '15px',
    fontWeight: 'bold',
    color: '#2e7d32',
    marginTop: '5px',
  },
};

export default Mobile;