import React from 'react';
import { useNavigate } from 'react-router-dom';

const ElectronicsProducts = [
  {
    name: 'Refrigerator',
    price: '₹29,999',
    image: 'data:image/jpeg;base64,',
  },
  {
    name: 'Washing Machine',
    price: '₹4,999',
    image: 'data:image/jpeg;base64,',
  },
  {
    name: 'Gaming Mouse',
    price: '₹999',
    image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTM2CPkxCBikLjRBvoSQnnUV-o5ZdU29DGKBA&s',
  },
  {
    name: 'Camera',
    price: '₹9,999',
    image: 'https://images-cdn.ubuy.co.in/65fe64a89de64a706c0120dc-canon-eos-5d-mark-iv-dslr-camera-with.jpg',
  },
  {
    name: 'LED TV',
    price: '₹13,999',
    image: 'https://images.samsung.com/is/image/samsung/p6pim/in/ua43t5410akxxl/gallery/in-fhd-t5310-ua43t5410akxxl-532972655?$684_547_PNG$',
  },
  {
    name: 'Headphone',
    price: '₹25,999',
    image: 'https://www.boat-lifestyle.com/cdn/shop/products/main2_b66dce6b-710d-49cb-9d1c-2bc8c9c0ab15.png?v=1645698328',
  },
];

const Electronics = () => {
  const navigate = useNavigate();

  const handleProductClick = (product) => {
    // Example: Navigate to product details or virtual try-on
    console.log('Clicked:', product.name);
    navigate('/product-details', { state: { product } }); // optional route for expansion
  };

  return (
    <div style={styles.container}>
      <h2 style={styles.heading}>Electronics</h2>
      <div style={styles.grid}>
        {ElectronicsProducts.map((product, index) => (
          <div
            key={index}
            style={styles.card}
            onClick={() => handleProductClick(product)}
          >
            <img
              src={product.image}
              alt={product.name}
              style={styles.image}
            />
            <div style={styles.name}>{product.name}</div>
            <div style={styles.price}>{product.price}</div>
          </div>
        ))}
      </div>
    </div>
  );
};

const styles = {
  container: {
    padding: '20px',
    backgroundColor: '#f4f6f8',
    minHeight: '100vh',
  },
  heading: {
    textAlign: 'center',
    marginBottom: '20px',
    fontSize: '28px',
    color: '#333',
  },
  grid: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'center',
    gap: '20px',
  },
  card: {
    backgroundColor: '#fff',
    width: '200px',
    padding: '15px',
    borderRadius: '12px',
    boxShadow: '0 4px 8px rgba(0,0,0,0.1)',
    textAlign: 'center',
    cursor: 'pointer',
    transition: 'transform 0.2s ease',
  },
  image: {
    width: '100%',
    height: '180px',
    objectFit: 'contain',
    marginBottom: '10px',
  },
  name: {
    fontSize: '16px',
    fontWeight: '600',
    color: '#333',
  },
  price: {
    fontSize: '15px',
    fontWeight: 'bold',
    color: '#2e7d32',
    marginTop: '5px',
  },
};

export default Electronics;