import React from 'react';
import { useNavigate } from 'react-router-dom';

const homeProducts = [
  {
    name: 'couch',
    price: '₹1,29,999',
    image: 'data:image/jpeg;base64,',
  },
  {
    name: 'Dining',
    price: '₹24,999',
    image: 'data:image/jpeg;base64,',
  },
  {
    name: 'decaration product',
    price: '₹69,999',
    image: 'data:image/jpeg;base64,',
  },
  {
    name: 'bed',
    price: '₹56999',
    image: 'data:image/jpeg;base64,',
  },
  {
    name: 'clock',
    price: '₹13,999',
    image: 'data:image/jpeg;base64,',
  },
  {
    name: 'Smart Home Product',
    price: '₹25,999',
    image: 'https://www.google.com/imgres?q=homeproducts%20image&imgurl=https%3A%2F%2Flifehacker.com%2Fimagery%2Farticles%2F01HS5BKJMN9TRMXA4PWYBTRADB%2Fhero-image.fill.size_1248x702.v1727709804.jpg&imgrefurl=https%3A%2F%2Flifehacker.com%2Ftech%2Feverything-to-consider-before-buying-smart-home-tech&docid=fYdUAAR8wzLvyM&tbnid=YznG4CtDoBwvhM&vet=12ahUKEwiy4_uTwa2OAxXr4TgGHeciFO04ChAzegQIBhAA..i&w=1248&h=702&hcb=2&ved=2ahUKEwiy4_uTwa2OAxXr4TgGHeciFO04ChAzegQIBhAA',
  },
];

const Home1 = () => {
  const navigate = useNavigate();

  const handleProductClick = (product) => {
    // Example: Navigate to product details or virtual try-on
    console.log('Clicked:', product.name);
    navigate('/product-details', { state: { product } }); // optional route for expansion
  };

  return (
    <div style={styles.container}>
      <h2 style={styles.heading}>Home</h2>
      <div style={styles.grid}>
        {homeProducts.map((product, index) => (
          <div
            key={index}
            style={styles.card}
            onClick={() => handleProductClick(product)}
          >
            <img
              src={product.image}
              alt={product.name}
              style={styles.image}
            />
            <div style={styles.name}>{product.name}</div>
            <div style={styles.price}>{product.price}</div>
          </div>
        ))}
      </div>
    </div>
  );
};

const styles = {
  container: {
    padding: '20px',
    backgroundColor: '#f4f6f8',
    minHeight: '100vh',
  },
  heading: {
    textAlign: 'center',
    marginBottom: '20px',
    fontSize: '28px',
    color: '#333',
  },
  grid: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'center',
    gap: '20px',
  },
  card: {
    backgroundColor: '#fff',
    width: '200px',
    padding: '15px',
    borderRadius: '12px',
    boxShadow: '0 4px 8px rgba(0,0,0,0.1)',
    textAlign: 'center',
    cursor: 'pointer',
    transition: 'transform 0.2s ease',
  },
  image: {
    width: '100%',
    height: '180px',
    objectFit: 'contain',
    marginBottom: '10px',
  },
  name: {
    fontSize: '16px',
    fontWeight: '600',
    color: '#333',
  },
  price: {
    fontSize: '15px',
    fontWeight: 'bold',
    color: '#2e7d32',
    marginTop: '5px',
  },
};

export default Home1;